import '../estilo/estilob.css';
import 'bootstrap/dist/css/bootstrap.min.css'
import portada from '../assets/img/Programming-amico.svg';
import Carousel from'./Carousel';
import Carusela from './Carusela';
import { Link, Outlet } from 'react-router-dom';
const Home = () => {
  return (

    <div className="principal">
		<div className="panel1">
			<div className="in-icon">
				<h2>Lenguajes de Programacio</h2>
				<p> “Un programador que escriba un código limpio, entiende perfectamente el problema antes de escribir el código”.</p>
				
				<div className="icons">
					<i className='bx bxl-google'></i>
					<i className='bx bxl-facebook-circle' ></i>
					<i className='bx bxl-twitter' ></i>	
				</div>
				<Link to="Contact" className='enlaces'><input type="button" value="Iniciar Sesion" id="sign-in"></input></Link>
			</div>
		</div>
	
		<div class="panel2">
		
					<div className='carusel-p1'><Carousel></Carousel></div>
          			<div className='caja-p1'><img src={portada} alt=""/></div>
				
			
		</div>
	</div>
	
  );
}
export default Home;
