import sobre from '../assets/img/sobre.png';
import goimg from '../assets/img/project-img5.png';
import ejgo from '../assets/img/ejemplogo.png'
const Pgo = () => {
  return(
    <>
      <div className="principal">
          <div className="panel1">
              <div className="in-icon">
                  <div><h2>¿QUE ES EL LENGUAJE GO</h2>
                     <p>El Lenguaje Go, al igual que C y C++, es un lenguaje compilado y concurrente, o en otras palabras: soporta canales de comunicación basados en el lenguaje CSP.</p>
                  </div>
                  <div className='escudo '><img src={goimg} alt=""/></div>
                  <div className="icons">
                      <i className='bx bxl-google'></i>
                      <i className='bx bxl-facebook-circle' ></i>
                      <i className='bx bxl-twitter' ></i>	
                  </div>
              </div>
              
          </div>
      
          <div class="panel2">
                <div className='historia-prog'>
                    <h6>Número de trabajos:1,700</h6>

                    <p>Beneficios</p>
                    <p>También conocido como Golang, Go fue desarrollado por Google para ser un lenguaje eficiente, legible y seguro para la programación a nivel de sistema. Funciona bien para sistemas distribuidos, en los que los sistemas están ubicados en diferentes redes y necesitan comunicarse enviando mensajes entre sí. Si bien es un lenguaje relativamente nuevo, Go tiene una gran biblioteca de estándares y una extensa documentación.</p>
                    <p>Inconvenientes</p>
                    <p>Go no ha ganado un uso generalizado fuera de Silicon Valley. Go no incluye una biblioteca para interfaces gráficas de usuario, que son las formas más comunes en que los usuarios finales interactúan con cualquier dispositivo que tenga una pantalla.</p>
                    <p>Usos comunes</p>
                    <p>Go se usa principalmente para aplicaciones que necesitan procesar una gran cantidad de datos. Además de Google, las empresas que utilizan Go para ciertas aplicaciones incluyen Netflix, Twitch y Uber.</p>             
                                        
                </div> 
          </div>
      </div>
      <div className="principal">
          <div className="panel1">
              <div className="in-icon">
                  <div><h2>Un ejemplo  de GO</h2>
                      <p>Carrera de Informatica</p>
                  </div>
                  <div className='escudo '><img src={sobre} alt=""/></div>
                  
               </div>
              
          </div>
      
          <div class="panel2">
                <div className='texto'>
                        <div>
                            <img src={ejgo} alt=""/>
                        </div>
                   
                </div> 
          </div>
      </div>
      </>
  );
}
export default Pgo;
