import sobre from '../assets/img/sobre.png';
import csharpimg from '../assets/img/project-img4.png';
import ejgo from '../assets/img/ejemploCsharp.png'
const Pcsharp = () => {
  return(
    <>
      <div className="principal">
          <div className="panel1">
              <div className="in-icon">
                  <div><h2>¿QUE ES CSHARP?</h2>
                     <p>Desarrollado por Microsoft para agilizar C (como competidor de Java), se integra con el software .NET de Microsoft y se usa para el desarrollo de juegos, programación para el lado del servidor, aplicaciones móviles y para Windows. Tiene una sintaxis similar a C, C+ y Java.</p>
                  </div>
                  <div className='escudo '><img src={csharpimg} alt=""/></div>
                  <div className="icons">
                      <i className='bx bxl-google'></i>
                      <i className='bx bxl-facebook-circle' ></i>
                      <i className='bx bxl-twitter' ></i>	
                  </div>
              </div>
              
          </div>
      
          <div class="panel2">
                <div className='historia-prog'>
                    <h6>Número de trabajos: 18,000</h6>

                    <h4>Beneficios</h4>
                    <p>Microsoft desarrolló C# como una variante más rápida y segura de C. Está totalmente integrado con el marco de software .NET de Microsoft, que admite el desarrollo de aplicaciones para Windows, complementos de navegador y dispositivos móviles. C# ofrece bases de código compartidas, una gran biblioteca de código y una variedad de tipos de datos.</p>
                    <h4>Inconvenientes</h4>
                    <p>C# puede tener una curva de aprendizaje pronunciada, especialmente para depurar errores. También es menos flexible que lenguajes como C++.</p>
                    <h4>Usos comunes</h4>
                    <p>C# es el lenguaje de referencia para el desarrollo de aplicaciones de Microsoft y Windows. También se puede usar para dispositivos móviles y consolas de videojuegos usando una extensión de .NET Framework llamada Mono.</p>             
                                        
                </div> 
          </div>
      </div>
      <div className="principal">
          <div className="panel1">
              <div className="in-icon">
                  <div><h2>Un ejemplo de C#</h2>
                      <p>Carrera de Informatica</p>
                  </div>
                  <div className='escudo '><img src={sobre} alt=""/></div>
               </div>
              
          </div>
      
          <div class="panel2">
                <div className='texto'>
                         <div>
                            <img src={ejgo} alt=""/>
                        </div>              
                     
                </div> 
          </div>
      </div>
      </>
  );
}
export default Pcsharp;
