import sobre from '../assets/img/sobre.png';
import javaimg from '../assets/img/project-img2.png';
import ejgo from '../assets/img/ejemplojava.png'
const Pjava = () => {
  return(
    <>
      <div className="principal">
          <div className="panel1">
              <div className="in-icon">
                  <div><h2>¿QUE ES EL LENGUAJE JAVA?</h2>
                     <p>Un lenguaje de programación de propósito general, orientado a objetos, cuyo espíritu se resume en las siglas WORA: Written Once, Run Anywhere</p>
                  </div>
                  <div className='escudo '><img src={javaimg} alt=""/></div>
                  <div className="icons">
                      <i className='bx bxl-google'></i>
                      <i className='bx bxl-facebook-circle' ></i>
                      <i className='bx bxl-twitter' ></i>	
                  </div>
              </div>
              
          </div>
      
          <div class="panel2">
                <div className='historia-prog'>
                    <h6>Número de trabajos: 29,000</h6>

                    <p>Beneficios</p>
                    <p>Java es el lenguaje de programación más comúnmente asociado con el desarrollo de aplicaciones cliente-servidor, que son utilizadas por grandes empresas de todo el mundo. Java está diseñado para ser un lenguaje de programación débilmente acoplado, lo que significa que una aplicación escrita en Java puede ejecutarse en cualquier plataforma que admita Java. Como resultado, Java se describe como el lenguaje de programación de "escribir una vez, ejecutar en cualquier lugar".</p>
                    <p>Inconvenientes</p>
                    <p>Java no es ideal para aplicaciones que se ejecutan en la nube, a diferencia del servidor (que es común para las aplicaciones comerciales). Además, la empresa de software Oracle, propietaria de Java, cobra una tarifa de licencia para usar el kit de desarrollo de Java.</p>
                    <p>Usos comunes</p>
                    <p>Junto con las aplicaciones comerciales, Java se usa ampliamente en el sistema operativo móvil Android.</p>             
                                        
                </div> 
          </div>
      </div>
      <div className="principal">
          <div className="panel1">
              <div className="in-icon">
                  <div><h2>Un ejemplo de Java</h2>
                      <p>Carrera de Informatica</p>
                  </div>
                  <div className='escudo '><img src={sobre} alt=""/></div>
               </div>
              
          </div>
      
          <div class="panel2">
                <div className='texto'>
                        <div>
                            <img src={ejgo} alt=""/>
                        </div>             
                     
                </div> 
          </div>
      </div>
      </>
  );
}
export default Pjava;
