import sobre from '../assets/img/sobre.png';
import phpimg from '../assets/img/project-img6.png';
import ejgo from '../assets/img/ejemplophp.png'

const Pphp = () => {
  return(
    <>
      <div className="principal">
          <div className="panel1">
              <div className="in-icon">
                  <div><h2>¿QUE ES EL LENGUAJE PHP?</h2>
                     <p>PHP es un lenguaje de elaboración de comandos (scripting) del lado del servidor, desarrollado desde mediados de los años 90 y usado para crear sitios web interactivos y dinámicos. Utilizado por muchísimos programadores. </p>
                  </div>
                  <div className='escudo '><img src={phpimg} alt=""/></div>
                  <div className="icons">
                      <i className='bx bxl-google'></i>
                      <i className='bx bxl-facebook-circle' ></i>
                      <i className='bx bxl-twitter' ></i>	
                  </div>
              </div>
              
          </div>
      
          <div class="panel2">
                <div className='historia-prog'>
                    <h6>Número de trabajos:7,000</h6>

                    <h4>Beneficios</h4>
                    <p>PHP se usa ampliamente para el desarrollo web del lado del servidor, cuando un sitio web solicita información de un servidor con frecuencia. Como lenguaje más antiguo, PHP se beneficia de un gran ecosistema de usuarios que han producido marcos, bibliotecas y herramientas de automatización para hacer que el lenguaje de programación sea más fácil de usar. El código PHP también es fácil de depurar.</p>
                    <h4>Inconvenientes</h4>
                    <p>A medida que Python y JavaScript ganaron popularidad, la popularidad de PHP disminuyó. PHP también es conocido por sus vulnerabilidades de seguridad. De acuerdo con Indeed, la mayoría de los programadores de PHP toman roles a corto plazo que duran menos de un año.</p>
                    <h4>Usos comunes</h4>
                    <p>PHP es el código que ejecuta sitios web orientados al contenido como Facebook, WordPress y Wikipedia.</p>             
                                        
                </div> 
          </div>
      </div>
      <div className="principal">
          <div className="panel1">
              <div className="in-icon">
                  <div><h2>Un ejemplo de PhP</h2>
                      <p>Carrera de Informatica</p>
                  </div>
                  <div className='escudo '><img src={sobre} alt=""/></div>
               </div>
              
          </div>
      
          <div class="panel2">
                <div className='texto'>
                        <div>
                            <img src={ejgo} alt=""/>
                        </div>               
                     
                </div> 
          </div>
      </div>
      </>
  );
}
export default Pphp;
