import 'bootstrap/dist/css/bootstrap.min.css'
function llamar(){

    const btnSignIn = document.getElementById("sign-in"),
      btnSignUp = document.getElementById("sign-up"),
      formRegister = document.querySelector(".register"),
      formLogin = document.querySelector(".login");

btnSignIn.addEventListener("click", e =>{
    formRegister.classList.add("hide");
    formLogin.classList.remove("hide");
})

btnSignUp.addEventListener("click", e =>{
    formLogin.classList.add("hide");
    formRegister.classList.remove("hide");
})
}



const Contact = () => {


  return(

        <div className='cuerpo'>
        <div class="container-form register">
        <div class="information">
            <div class="info-childs">
                <h2>¡¡Bienvenido!!</h2>
                <p>Para unirte a nuestra comunidad por favor Inicia Sesión con tus datos</p>
                <input type="button" onClick={llamar} void value="Iniciar Sesion" id="sign-in"></input>
            </div>
        </div>

        <div class="form-information">
            <div class="form-information-child">
                    <h2>Crea una cuenta</h2>
                <div class="icons">                   
                    <i class='bx bxl-google'></i>
                    <i class='bx bxl-facebook-circle' ></i>
                    <i class='bx bxl-twitter' ></i>	
                </div>			
                <p>usa tu email para registrarte</p>
                <form  class="form">
                    <label><i class='bx bx-user' ></i>
                        <input type="text" placeholder="Nombre Completo"></input>
                    </label>
                    <label><i class='bx bx-envelope' ></i>
                        <input type="email" placeholder="Correo Electronico"></input>
                    </label>
                    <label>
                        <i class='bx bx-lock-open-alt' ></i>
                        <input type="password" placeholder="Contraseña"></input>
                    </label>
                    <input type="submit" value="Registrarse"></input>
                </form>
            </div>
        </div>
    </div>


    <div class="container-form login hide">
        <div class="information">
            <div class="info-childs">
                <h2>¡¡Bienvenido nuevamente!!</h2>
                <p>Si aun no tienes una cuenta por favor registrate aquí..</p>
                <input type="button" value="Registrarse" id="sign-up"></input>
            </div>
        </div>
        <div class="form-information ">
            <div class="form-information-child">
                    <h2>Iniciar Sesión</h2>
                <div class="icons">
                    
                    <i class='bx bxl-google'></i>
                    <i class='bx bxl-facebook-circle' ></i>
                    <i class='bx bxl-twitter' ></i>	
                </div>			
                <p>o Iniciar Sesión con una cuenta</p>
                <form  class="form">
                    
                    <label><i class='bx bx-envelope' ></i>
                        <input type="email" placeholder="Correo Electronico"></input>
                    </label>
                    <label>
                        <i class='bx bx-lock-open-alt' ></i>
                        <input type="password" placeholder="Contraseña"></input>
                    </label>
                    <input type="submit" value="Iniciar Sesión"></input>
                </form>
            </div>
        </div>
    </div>
    <script>
      
    </script>
    </div>

  );
}
export default Contact;
