import sobre from '../assets/img/sobre.png';
import Cimg from '../assets/img/project-img1.png';
import ejgo from '../assets/img/ejemplocmas.png'
const Pcmas = () => {
  return(
    <>
      <div className="principal">
          <div className="panel1">
              <div className="in-icon">
                  <div><h2>¿QUE ES EL LENGUAJE C++?</h2>
                     <p>Es uno de los primeros lenguajes de programación jamás creados. C fue lanzado en la década de 1970, mientras que C ++ apareció en la década de 1980 como una extensión de C que también introdujo clases y varias características adicionales, incluidas las funcionalidades orientadas a objetos. Además, estos dos lenguajes han permitido desarrollar otros más modernos: Python y PHP.</p>
                  </div>
                  <div className='escudo '><img src={Cimg} alt=""/></div>
                  <div className="icons">
                      <i className='bx bxl-google'></i>
                      <i className='bx bxl-facebook-circle' ></i>
                      <i className='bx bxl-twitter' ></i>	
                  </div>
              </div>
              
          </div>
      
          <div class="panel2">
                <div className='historia-prog'>
                    <h6>Número de trabajos: 9,000</h6>

                    <p>Beneficios</p>
                    <p>C++ es una extensión de C que funciona bien para programar los sistemas que ejecutan aplicaciones, a diferencia de las aplicaciones mismas. C++ también funciona bien para sistemas multidispositivo y multiplataforma. Con el tiempo, los programadores han escrito un gran conjunto de bibliotecas y compiladores para C++. Ser capaz de usar estas utilidades de manera efectiva es tan importante para comprender un lenguaje de programación como escribir código. </p>
                    <p>Inconvenientes</p>
                    <p>Al igual que C, C++ tiene una sintaxis compleja y una gran cantidad de funciones que pueden complicar las cosas para los nuevos programadores. C++ tampoco admite la verificación en tiempo de ejecución, que es un método para detectar errores o defectos mientras se ejecuta el software.</p>
                    <p>Usos comunes</p>
                    <p>C++ tiene muchos usos y es el lenguaje detrás de todo, desde juegos de computadora hasta simulaciones matemáticas.</p>             
                                        
                </div> 
          </div>
      </div>
      <div className="principal">
          <div className="panel1">
              <div className="in-icon">
                  <div><h2>Un ejemplo de C++</h2>
                      <p>Carrera de Informatica</p>
                  </div>
                  <div className='escudo '><img src={sobre} alt=""/></div>
               </div>
              
          </div>
      
          <div class="panel2">
                <div className='texto'>
                        <div>
                            <img src={ejgo} alt=""/>
                        </div>         
                     
                </div> 
          </div>
      </div>
      </>
  );
}
export default Pcmas;
