import sobre from '../assets/img/sobre.png';
import pytonimg from '../assets/img/project-img3.png';
import ejgo from '../assets/img/ejemplopython.png'
const Ppyton = () => {
  return(
    <>
      <div className="principal">
          <div className="panel1">
              <div className="in-icon">
                  <div><h2>¿QUE ES EL LENGUAJE PYTHON?</h2>
                     <p>Desarrollado por Guido van Rossum en los años 90, Python es uno de los lenguajes de programación más populares del mundo. Sigue teniendo tanto éxito gracias a su facilidad de uso, que permite a principiantes aprender rápidamente cómo crear programas sencillos.</p>
                  </div>
                  <div className='escudo '><img src={pytonimg} alt=""/></div>
                  <div className="icons">
                      <i className='bx bxl-google'></i>
                      <i className='bx bxl-facebook-circle' ></i>
                      <i className='bx bxl-twitter' ></i>	
                  </div>
              </div>
              
          </div>
      
          <div class="panel2">
                <div className='historia-prog'>
                    <h6>Número de trabajos: 19,000</h6>

                    <h4>Beneficios</h4>
                    <p>Python es ampliamente considerado como un lenguaje de programación fácil de aprender, debido a su sintaxis sencilla, la gran biblioteca de estándares, herramientas e integración con otros lenguajes de programación populares como C y C++. Es un lenguaje de programación popular, especialmente entre los nuevos emprendimientos y, por lo tanto, las habilidades en Python tienen una gran demanda.</p>
                    <h4>Inconvenientes</h4>
                    <p>Python no es adecuado para el desarrollo de aplicaciones móviles.</p>
                    <h4>Usos comunes</h4>
                    <p>Python se utiliza en una amplia variedad de aplicaciones, incluida la inteligencia artificial, los servicios financieros y la ciencia de datos. Paginas de redes sociales como Instagram y Pinterest también se basan en Python.</p>             
                                        
                </div> 
          </div>
      </div>
      <div className="principal">
          <div className="panel1">
              <div className="in-icon">
                  <div><h2>Un ejemplo de Phython</h2>
                      <p>Carrera de Informatica</p>
                  </div>
                  <div className='escudo '><img src={sobre} alt=""/></div>
               </div>
              
          </div>
      
          <div class="panel2">
                <div className='texto'>
                        <div>
                            <img src={ejgo} alt=""/>
                        </div>         
                     
                </div> 
          </div>
      </div>
      </>
  );
}
export default Ppyton;
