import React from 'react';
import { Navbar, Container, Nav, NavDropdown} from 'react-bootstrap';
import { useState, useEffect } from 'react';
import { Link, Outlet } from 'react-router-dom';

import logo from '../assets/img/logo3.png';

import navIcon2 from '../assets/img/nav-icon2.svg'
import navIcon3 from '../assets/img/nav-icon3.svg'
import PiePag from './PiePag';


export const NavBar = () => {

    const [scrolled, setScrolled] =useState(false);

    //activar seter valores
    const [activeLink, setActiveLink]=useState('home');


    useEffect(()=>{
        const onScroll= ()=>{
            if (window.scrollY > 50) {
                setScrolled(true);
            } else {
                setScrolled(true);
            }
        }    
        window.addEventListener("scroll", onScroll);
        return ()=>window.removeEventListener("scroll", onScroll);
    },[])

    const onUpdateActiveLink = (value)=>{
        setActiveLink(value);
    }
    return(
        <>
        <Navbar id='contenido' expand="lg" className={scrolled ? "scrolled":""}>
            <Container>
                <Navbar.Brand href="/">
                    <img  src={logo} alt="Logo"/>
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav">
                    <span className="navbar-toggler-icon"></span>
                </Navbar.Toggle>
        
                <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="me-auto">
                    <Nav.Link  className={activeLink === 'home' ? 'active navbar-link' : 'navbar-link'} onClick={()=> onUpdateActiveLink('home')}>
                        <Link to="/" className='enlaces'>Home</Link>
                    </Nav.Link>
                    <Nav.Link  className={activeLink === 'skills' ? 'active navbar-link' : 'navbar-link'} onClick={()=> onUpdateActiveLink('skills')}>
                        <Link to="About" className='enlaces'>About</Link>    
                    </Nav.Link>
                    <Nav.Link  className={activeLink === 'project' ? 'active navbar-link' : 'navbar-link'} onClick={()=> onUpdateActiveLink('projects')}>
                        <Link to="Contact" className='enlaces'>Contact</Link>
                    </Nav.Link>
    
                   <NavDropdown className='mast' title="Mas Lenguages"  id="basic-nav-dropdown">
                         <Link to="Pjava" className='enlaces'>
                        <NavDropdown.Item href="#action/3.1">
                           Lenguaje Java
                        </NavDropdown.Item></Link>
                        <Link to="Pcsharp" className='enlaces'>
                         <NavDropdown.Item src="/" href="#action/3.2">
                           Lenguaje CSharp
                        </NavDropdown.Item></Link>
                        <Link to="Ppyton" className='enlaces'>
                        <NavDropdown.Item href="#action/3.3">
                           Lenguaje PytHon
                        </NavDropdown.Item></Link>
                        <Link to="Pcmas" className='enlaces'>
                        <NavDropdown.Item href="#action/3.1">
                           Lenguaje C++
                        </NavDropdown.Item></Link>
                        <Link to="Pgo" className='enlaces'>
                        <NavDropdown.Item href="#action/3.2">
                           Lenguaje Go
                        </NavDropdown.Item></Link>
                        <Link to="Pphp" className='enlaces'>
                        <NavDropdown.Item href="#action/3.3">
                          Lenguage PHP
                        </NavDropdown.Item></Link>
                        <Link to="Creditos" className='enlaces'> 
                        <NavDropdown.Divider />
                        <NavDropdown.Item href="#action/3.4">
                            Creditos
                        </NavDropdown.Item></Link>
                   </NavDropdown>    

                </Nav>
                <span className="navbar-text">
                    <div className="social-icon">
                        
                        <a href="https://www.facebook.com/CarreraDeInformaticaUmsa/?locale=es_LA"> <img src={navIcon2} alt=""/></a>
                        <a href="https://www.google.com/intl/es/account/about/"> <img src={navIcon3} alt=""/></a>
                    </div>
                    <Link to="Contact" className='enlaces'><button className="vvd" onClick={()=>console.log('Conectado')}><span>Iniciar Sesión</span></button></Link>
                </span>
                </Navbar.Collapse>
            </Container>
        </Navbar>
        <Outlet></Outlet> 
       
        
        </>  
    )
}
