import umsa1 from '../assets/img/umsa1.jpg'
import umsa2 from '../assets/img/umsa2.jpg'
import umsa3 from '../assets/img/umsa3.jpg'
const Carusela = () => {
    return (
        <div>
            <div id="carouselExampleCaptions2" className="carousel slide">
            <div className="carousel-indicators">
                <button type="button" data-bs-target="#carouselExampleCaptions2" data-bs-slide-to="0" className="active" aria-current="true" aria-label="Slide 1"></button>
                <button type="button" data-bs-target="#carouselExampleCaptions2" data-bs-slide-to="1" aria-label="Slide 2"></button>
                <button type="button" data-bs-target="#carouselExampleCaptions2" data-bs-slide-to="2" aria-label="Slide 3"></button>
            </div>
            <div className="carousel-inner">
                <div className="carousel-item active" data-bs-interval="200">
                <img src={umsa1} className="d-block w-100" alt="First slide"/>
                <div className="carousel-caption d-none d-md-block">
                   
                </div>
                </div>
                <div className="carousel-item" data-bs-interval="200">
                <img src={umsa2} className="d-block w-100" alt="Second slide"/>
                <div class="carousel-caption d-none d-md-block">
                    
                </div>
                </div>
                <div className="carousel-item" data-bs-interval="200">
                <img src={umsa3} class="d-block w-100" alt="Third slide"/>
                <div class="carousel-caption d-none d-md-block">
                    
                </div>
                </div>
            </div>
            <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleCaptions2" data-bs-slide="prev">
                <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                <span className="visually-hidden">Previous</span>
            </button>
            <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleCaptions2" data-bs-slide="next">
                <span className="carousel-control-next-icon" aria-hidden="true"></span>
                <span className="visually-hidden">Next</span>
            </button>
            </div>
        </div>
    );
  }
  export default Carusela;
  