import '../estilo/estilob.css';
import sobre from '../assets/img/sobre.png';
import Multimedia from './Multimedia';
const About = () => {
  return(
    <>
      <div className="principal">
          <div className="panel1">
              <div className="in-icon">
                  <div><h2>¿Que son los lenguajes de programacion?</h2>
                      <p>Carrera de Informatica</p>
                  </div>
                  <div className='escudo '><img src={sobre} alt=""/></div>
                  <div className="icons">
                      <i className='bx bxl-google'></i>
                      <i className='bx bxl-facebook-circle' ></i>
                      <i className='bx bxl-twitter' ></i>	
                  </div>
              </div>
              
          </div>
      
          <div class="panel2">
                <div className='video'>
                    <Multimedia></Multimedia>              
                     
                </div> 
          </div>
      </div>
      <div className="principal">
          <div className="panel1">
              <div className="in-icon">
                  <div><h2>Pequeño concepto de lo que es un lenguaje de programacion</h2>
                      <p>Carrera de Informatica</p>
                  </div>
                  <div className='escudo '><img src={sobre} alt=""/></div>
               </div>
              
          </div>
      
          <div class="panel2">
                <div className='texto'>
                        <p>Un lenguaje de programación es una forma de comunicarnos con una computadora, tablet o celular e indicarle qué queremos hacer.</p>
                        <p>Existen distintos tipos de lenguaje: principalmente de bajo nivel y de alto nivel. La diferencia se encuentra en lo cerca o lejos que estemos del hardware de nuestro equipo. Esta cercanía tiene que ver con el control que tengamos sobre el dispositivo, placa o controlador.</p>
                        <p>Los programas habitualmente se diseñan o escriben a través de un entorno de desarrollo integrado (IDE por sus siglas en ingles)</p>
                        <div className='txt-lista'>
                        Algunos ejemplos son:
                            <ul>
                            <li>Eclipse: <a href='http://www.eclipse.org'>www.eclipse.org</a></li>
                            <li>icrosoft Visual Studio: <a href='http://www.visualstudio.com'>www.visualstudio.com</a></li>
                            <li>Android Studio:<a href='https://developer.android.com/?hl=es-419'> www.developer.android.com</a></li>
                            <li>Netbeans:<a href='http://www.netbeans.org'> www.netbeans.org</a></li>
                            </ul>
         
                        </div>                    
                     
                </div> 
          </div>
      </div>
      </>
  );
}
export default About;
